.menu-row{
  background-color: #11052C;
  top:5rem;
  width: 0rem;
  position: fixed;
  z-index: 2;
}

.toggleDropdown{
  position: fixed;
  top:5rem;
  outline: none;
  z-index: 1;
  height: 40rem;
}


.toggle{
  width: 15rem;
}

.toggleButton{
  left:15rem;
  outline: none;
}

.btn:focus {
  outline: none !important;
  box-shadow: none;
}

.card_1{
  background-color: blue;
  width: 25rem;
  height: 18rem;
  position: relative;
  margin-top: 2rem;
  background-size: contain;
}

.card_1 .info{
  position: absolute;
  bottom:0px;
  width:25rem;
  height: 5rem;
  border-style:outset none none none;
  border-width: thin;
  border-color: white;
  color: #FEF7DC;
}

.weatherDataForm{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
}

.align-middle {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  transition: 0.5s;
}

.column {
  margin-top: 3rem;
  padding-left: 3rem;
  transition: 0.5s;
}
.column:hover {
  padding-left: 0;
  transition: 0.5s;
}
.card_2:hover{
  background: linear-gradient(170deg, #b4ec51 0%, #429321 100%) !important;
  transition: 0.5s;
}
.column:hover .card_2 .txt {
  margin-left: 1rem;
  transition: 0.5s;
}
.column:hover .card_2 .txt h1,
.column:hover .card_2 .txt p {
  color: white;
  opacity: 1;
  transition: 0.5s;
}
.column:hover a {
  color: white;
  transition: 0.5s;
}
.column:hover a:after {
  width: 10%;
  transition: 0.5s;
}

.card_2 {
  height: 28rem;
  margin: 0;
  padding: 1.7rem 1.2rem;
  border: none;
  border-radius: 0;
  color: black;
  letter-spacing: 0.05rem;
  font-family: "Oswald", sans-serif;
  box-shadow: 0 0 21px rgba(0, 0, 0, 0.27);
  transition: 0.5s;
}
.card_2 .txt {
  margin-left: -4rem;
  z-index: 1;
  transition: 0.5s;
}
.card_2 .txt h1 {
  font-size: 1.5rem;
  font-weight: 300;
  color:#F3C583;
  text-transform: uppercase;
  transition: 0.5s;
  font-weight:400;
}
.card_2 .txt p {
  font-size: 0.7rem;
  font-family: "Open Sans", sans-serif;
  letter-spacing: 0rem;
  margin-top: 33px;
  opacity: 0;
  color: white;
  transition: 0.5s;
}
.card_2 a {
  z-index: 3;
  font-size: 0.7rem;
  color: black;
  margin-left: 1rem;
  position: relative;
  bottom: -0.5rem;
  text-transform: uppercase;
  transition: 0.5s;
}
.card_2 a:after {
  content: "";
  display: inline-block;
  height: 0.5em;
  width: 0;
  margin-right: -100%;
  margin-left: 10px;
  border-top: 1px solid white;
  transition: 0.5s;
}
.card_2 .ico-card {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  transition: 0.5s;
}
.card_2 i {
  position: relative;
  right: -55%;
  top: 80%;
  font-size: 12rem;
  line-height: 0;
  opacity: 0;
  color: white;
  z-index: 0;
  transition: 0.5s;
}

.card_2:hover i{
  z-index: 2;
  opacity: 0.2;
}

.heading-name{
  font-size:2.5rem;
  -webkit-text-stroke:0.2rem;
  mix-blend-mode:luminosity;
}

.gr-1 {
